.card_container{
    margin: auto;
    
   
   
}
.card_container>a{
    text-decoration: none;
    color: black
}





.card_img{
    transition: 0.3s ease-in;
    
    
}
.card_img>img{
    cursor: pointer;
    box-shadow: 5px 5px 10px 5px grey;
}

.card_img:hover {
    
    transform: translateY(-15px);
    
   

}


.card_description{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 10px;
    margin-right: 10px;
}

@media (max-width:800px){
    .card_img>img{
        cursor: pointer;
        box-shadow: 5px 5px 10px 5px grey;
        width:90%;
        height: auto;
    }

}